body {
  background: var(--primary-bg-color);
  font-family: Inter, sans-serif;
  transition: all .25s;
  overflow-x: hidden;
}

p, h2 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

.favorites-zero {
  display: none;
}

.favorites-zero.active {
  width: 375px;
  margin: 0 auto;
  padding: 256px 20px 0;
  display: flex;
}

.container {
  background: var(--primary-bg-color);
  width: 335px;
  transition: all .25s;
}

.favorites-icon {
  width: 68px;
  height: 58px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.favorites-text {
  text-align: center;
  width: 335px;
  margin-top: 24px;
  font-size: 14px;
  line-height: 18px;
  display: flex;
}

.category-list-favourites, .tags-container {
  gap: 18px;
  display: flex;
}

.favorites-tags {
  cursor: pointer;
  color: #05050533;
  background-color: #fff;
  border-color: #05050533;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  width: 132px;
  height: 46px;
  font-size: 14px;
  font-weight: 500;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.category-btn {
  cursor: pointer;
  color: #05050533;
  background-color: #fff;
  border-color: #05050533;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  min-width: 91px;
  height: 46px;
  font-size: 14px;
  font-weight: 500;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.favourites-list-btn .category-btn:hover, .favorites-tags:hover, .favourites-list-btn .category-btn:focus, .favorites-tags:focus {
  color: #f8f8f8;
  background-color: #9bb537;
  border: none;
}

.btn-page-change:hover {
  background-color: #9bb537;
}

.btn-page-change {
  cursor: pointer;
  border-color: #0505054d;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 18px;
  font-weight: 600;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

.page-change-favourite-list {
  justify-content: center;
  gap: 4px;
  display: flex;
  position: fixed;
  bottom: 5%;
}

.btn-num-favourites {
  gap: 8px;
  margin-left: 10px;
  margin-right: 10px;
  display: flex;
}

.green-btn-favourites {
  background-color: #9bb537;
  border: none;
}

.dark-btn-favourites {
  background-color: #050505;
  border: none;
}

.dark-btn-favourites:hover {
  background-color: #050505;
}

.btn-number-favourite {
  display: flex;
}

.hero-block {
  display: none;
}

@media screen and (width >= 320px) {
  .container {
    width: 320px;
  }

  .page-change-favourite-list {
    justify-content: flex-start;
    min-width: 335px;
    min-height: 40px;
    bottom: 3%;
  }
}

@media screen and (width >= 428px) {
  .container {
    width: 428px;
  }
}

@media screen and (width >= 450px) {
  .container {
    width: 450px;
  }

  .page-change-favourite-list {
    justify-content: center;
  }
}

@media screen and (width >= 768px) {
  .container {
    width: 768px;
    margin-left: 32px;
    margin-right: 32px;
  }

  .page-change-favourite-list {
    justify-content: center;
    left: 35%;
  }
}

@media screen and (width >= 1158px) {
  .container {
    width: 1158px;
    margin-left: 100px;
    margin-right: 100px;
  }

  .page-change-favourite-list {
    left: 50%;
  }
}
